import users from './api/users';

type Props = {
  todos: Todo[];
};
export const TodoList: React.FC<Props> = ({ todos }) => (
  <ul className="
    is-flex container
    is-flex-wrap-wrap"
  >
    {todos.map(todo => (
      <li key={todo.id} className="card m-4">
        <div>
          <span className="m-2">Task:</span>
          {todo.title}
        </div>
        <div>
          <span className="m-2">User:</span>
          {users.find(user => user.id === todo.userId)?.name}
        </div>
      </li>
    ))}
  </ul>
);
